            <br>
                                <p>The Asia-Pacific Intervarsity Chinese Debate Tournament is organized by the Nanyang Technological University Chinese Society, once known as the Intervarsity Chinese Debate Tournament, Singapore-Malaysia Open. In year 2004, the committee decided to expand the scale of the tournament to the Asia-Pacific level. Since then, we have attracted the best debate teams from various countries in the Asia Pacific region to take part in the competition. In 2006, the 2nd Intervarsity Chinese Debate Tournament was held successfully and it attracted a group of Taiwan enthusiasts to organize the 3rd tournament in July 2007.
                                    <br><br>The next four tournaments were successfully held by NTU Chinese Society. For the 7th tournament in 2015, we decided to expand the number of competing teams from 16 to 24, and it turned out to be a great success. The organizing committee has received overwhelming responses from various tertiary institutions.
                                    <br><br>As one of the top International Chinese Debate Tournaments in the world, we are inspired and have thus decided to bring The 8th Asia-Pacific Intervarsity Chinese Debate Tournament, to be held in July 2017 to a new milestone.
                                    <br><br>In the coming tournament, we have decided to make the following significant improvements. (i) The balloting for participants will be conducted through electronical system instead of a manually-drawn lot, (ii) Judges advisory committee which is formed by several well-known judges from all over the Asia Pacific will be formed to improve the quality of the debate tournament, (iii) a series of school tours to selected junior colleges or secondary schools will be conducted.
