<template>
<div class="container">
  <br>
  <h1 style="text-align:center"></h1>
  <hr>
  <div class="row">
    <div class="col-md-12" style="text-align:center; color:darkred;"><h2>{{user.uniNameCN}}</h2></div>
    <div class="col-md-4" style="text-align:center;padding:1em 0;">
        <h3><a style="text-decoration:none;"></a><span style="color:gray;"><br /></span></h3>
        <iframe src="http://free.timeanddate.com/clock/i5h4olht/n236/tlcn8/fn6/fs16/tt0/tm3/th1/ta1/tb4" frameborder="0" width="144" height="45"></iframe>
    </div>

    <div class="col-md-4" style="text-align:center;padding:1em 0;">
      <h3>
        <a style="text-decoration:none;"></a>
        <span style="color:gray;"><br/>{{user.region}}</span>
        <br>
        <span v-if="user.region == ''"><iframe src="http://free.timeanddate.com/clock/i5h4olht/n122/tlcn8/fn6/fs16/tt0/tm3/th1/ta1/tb4" frameborder="0" width="144" height="45"></iframe></span>
        <span v-else-if="user.region == ''"><iframe src="http://free.timeanddate.com/clock/i5h4olht/n236/tlcn8/fn6/fs16/tt0/tm3/th1/ta1/tb4" frameborder="0" width="144" height="45"></iframe></span>
        <span v-else-if="user.region == ''"><iframe src="http://free.timeanddate.com/clock/i5h4olht/n240/tlcn8/fn6/fs16/tt0/tm3/th1/ta1/tb4" frameborder="0" width="144" height="45"></iframe></span>
        <span v-else-if="user.region == ''"><iframe src="http://free.timeanddate.com/clock/i5h4olht/n33/tlcn8/fn6/fs16/tt0/tm3/th1/ta1/tb4" frameborder="0" width="144" height="45"></iframe></span>
        <span v-else-if="user.region == ''"><iframe src="http://free.timeanddate.com/clock/i5h4olht/n102/tlcn8/fn6/fs16/tt0/tm3/th1/ta1/tb4" frameborder="0" width="144" height="45"></iframe></span>
        <span v-else-if="user.region == ''"><iframe src="http://free.timeanddate.com/clock/i5h4olht/n241/tlcn8/fn6/fs16/tt0/tm3/th1/ta1/tb4" frameborder="0" width="144" height="45"></iframe></span>
        <span v-else-if="user.region == ''"><iframe src="http://free.timeanddate.com/clock/i5h4olht/n33/tlcn8/fn6/fs16/tt0/tm3/th1/ta1/tb4" frameborder="0" width="144" height="45"></iframe></span>
      </h3>
    </div>

    <div class="col-md-4" style="text-align:center;padding:1em 0;">
      <h3><a style="text-decoration:none;"></a><span style="color:gray;"><br />()</span></h3>
      <input placeholder="">
      <h6>{{simTimeStart}}</h6>
    </div>

    <br>

    <div class="panel panel-default" id="registerBody">
      <div class="panel-body" >
        <div>
          <center>
            <h3></h3>
            <p></p>
            <br>
              <button v-on:click="recordTime" class="btn btn-primary btn-block " id="register"></button>
            <br>
            <a href="apchinesedebate.com/user_dashboard" class="btn btn-lg"></a>
          </center>
        </div>
      </div>
    </div>
    
  </div>
</div>
</template>
<script>
import axios from 'axios';

export default
{
  name:'simlottery',
  created () {
    setInterval(() => this.now = new Date, 1000 * 60)
  },
  data(){
    return{
      pressed: 0,
      simTimeStart: 0,
      user: {},
      uniDetails: {},
      drawn: 1,
      //startTime: 0,
    }
  },
  computed: {
    now () {
      return new Date
    },
    authUser: function(){ if(this.$store.getters.authUser) return JSON.parse(this.$store.getters.authUser);},
  },
  mounted(){
    //this.getDrawn();
    
  },
  created(){
    // this.startTime();
    // this.showUser();
  },
  methods:{
    recordTime:function(){
      const data = { 'pressed' : 1} //pressed here is to notify backend that user pressed the button
      try{
        axios
          .put('api/time/simulation/store', data, {
            headers: { Authorization: "Bearer " + localStorage.getItem('token')}
          })
          .then(resp=>{
            if(resp.data.status == 200){
              //console.log(resp.data)
              alert("")
            }else if(resp.data.status == 304){ alert("")
            }else if(resp.data.status == 201){ alert("")}
          })
      }catch(e){console.log(e)
      }
    },
    startTime: function(){
      axios
        .get('api/time/simulation/start',{
          headers:{
              Authorization: "Bearer " + localStorage.getItem('token')
          }
        })
        .then(resp=>{
          //console.log(resp.data)
          if(resp.data.status == 200){
              this.simTimeStart = resp.data.simTimeStart;
              this.startTime = resp.data.simTimeStart1;
          }
        })
    },
    showUser: function(){
      axios
        .get('api/user',{headers: { Authorization: "Bearer " + localStorage.getItem('token')}})
        .then(resp=>{
          this.user = resp.data.user
          this.uniDetails = resp.data.uniDetails
          switch(this.user.region){
            case "Malaysia": this.user.region = ""; break;
            case "Singapore": this.user.region = ""; break;
            case "China": this.user.region = ""; break;
            case "Hong Kong": this.user.region = ""; break;
            case "Macau": this.user.region = ""; break;
            case "Taiwan": this.user.region = ""; break;
            case "Australia": this.user.region = ""; break;
            default: break;
          }

        })
    },
    getDrawn: function(){
      axios
        .get('api/time/official/getDraw',{
          headers:{
            Authorization: "Bearer " + localStorage.getItem('token')
          }
        })
        .then(resp=>{
            this.drawn = resp.data.drawn
        })       
    },
  },
  // watch:{
  //   currentTime: function(){
  //     this.timeNow = new Data()
  //     console.log(this.timeNow)
  //   }
  // }
}
</script>
<style scoped>
  .register{
    background-color: darkred
  }

  #registerBody{
    margin: 0 auto;
    position: relative;
  }
</style>



